.iconButton:hover {
  background-color: #c5c5c5 !important;
  box-shadow: 0 7px 7px rgba(0, 0, 0, 0.4);
}

.todo-card,
.iconButton,
.btn-delete {
  transition: all 0.3s !important;
}

.todo-card:hover {
  padding-top: 10px !important;
  padding-bottom: 10px !important;
}

.btn-delete:hover {
  background-color: #78280f !important;
}

